import React, { useEffect } from "react";
import Scroll from "./Scroll";
import Reza from "../assets/img/reza.jpg";
import { init, Configuration } from "ityped";

const config: Configuration = {
  strings: ["I'M A FRONT-END ENGINEER", "I DEVELOP INTERESTING STUFF"],
};

const Header = () => {
  useEffect(() => {
    const target = document.querySelector("#target")! as unknown;
    init(target as string, config);
  }, []);
  
  return (
    <header className="header">
      <div className="header__title">
        <img src={Reza} alt="Reza Pazan" />
        <h1>Hi, I'm Reza Pazan</h1>
        <h2 id="target"></h2>
      </div>
      <Scroll />
    </header>
  );
};

export default Header;
