import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { redirect } from "../utils/utils";
import { ISocialProps } from "../utils/types";

const SocialItem: React.FC<ISocialProps> = (props) => {
  return (
    <div
      className="footer__socials_item"
      onClick={() => redirect(props.url)}
    >
      <span className="footer__socials_item--title">{props.title}</span>
      <span className="footer__socials_item--icon">
        <FontAwesomeIcon icon={props.icon} size="lg" />
      </span>
    </div>
  );
};

export default SocialItem;
